import React from 'react';
import Home from "./Home";
import NavBar from "./NavBar";
import Banks from "./Banks";

class App extends React.Component{
    //the state contains a component_id which selects which component to show
    state={component_id:0}

    changeComponentId = (id) =>{
        this.setState({component_id:id})
    }
    render() {
        if (this.state.component_id===0){
            return(
                <div>
                    <NavBar changeId={this.changeComponentId}/>
                    <Home changeId={this.changeComponentId}/>
                </div>
            )
        }
        if (this.state.component_id===1){
            return(
                <div>
                    <NavBar changeId={this.changeComponentId}/>
                    <Banks/>
                </div>
            )
        }

    }

}

export default App